// import Generic from './Generic';

/**
 * Bulk import form.. Download templates files, then attach and upload.
 */
export default class BulkImportForm extends React.Component {

    constructor(props) {
        super(props);

        this.submitFile = this.submitFile.bind(this);

        this.fileInput = React.createRef();

        // unlikely that we will need to use isOpenOnMount
        // store state in this component so that parent doesn't have to re-render it.
        this.state = {
            isOpen: this.props.isOpenOnMount || false,
        };

        // async nature of state makes it better to not use state for this.
        this.isSubmitting = false;
    }

    shouldComponentUpdate(prevProps, prevState) {

        if (prevState.isOpen !== this.state.isOpen) {
            return true;
        }

        return false;
    }

    submitFile() {
        console.log("Submit file...", this.fileInput, this.state);

        if (this.isSubmitting) {
            alert("Please wait a few seconds for your last import to finish processing.");
            return;
        } else {
            this.isSubmitting = true;
        }

        let OrderForm = this.props.parent;
        let input = this.fileInput.current;
        let file = input && input.files && input.files[0] ? input.files[0] : false;

        if (!file) {
            this.isSubmitting = false;
            alert("Please attach the file that you want to import.");
            return;
        }

        let formData = new FormData();
        formData.append('ajax_action', 'bulk_import');
        formData.append('bulk_import_file', file);

        axios.post(OrderForm.config.ajax_url, formData, {
            useCredentials: true,
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(response => {

            this.isSubmitting = false;

            let data = response.data || {};

            let success = data.success || false;
            let items = Array.isArray( data.items ) || [];
            let errors = data.errors || [];

            console.log("Bulk Import Response", data);

            if ( errors || ! success || ! items ) {

                errors = errors ? errors : [ "[008] An unknown error occurred." ];

                // todo: show errors to the user...
                console.log(errors);

            } else {

                // in reality, 500 items is probably way too many, both for the client-side
                // and for server-side checkout. Expect 100+ to be very rare however.
                let count = 0;
                let cap = 500;
                let hitCap = false;

                items.forEach( item => {

                    count++;

                    if ( count > cap ) {
                        hitCap = true;
                    } else {
                        OrderForm._items_.push( OrderForm.generateNewItemObject( OrderForm._items_.length, OrderForm.generateItemKey(), {
                            initialState: item
                        } ) );
                    }
                } );

                setTimeout( () => {
                    if ( hitCap ) {
                        alert( count + " items were added. The number of items inserted reached the cap (" + cap + "). Items beyond the cap were not inserted." );
                    } else {
                        alert( count + " items were added." );
                    }
                });
            }

        })
            .catch(error => {
                this.isSubmitting = false;

                alert("[3] An unknown error occurred.");
                console.error("Bulk Import Error", error);
            });
    }

    render() {

        let open = this.state.isOpen;
        let downloadSrc = "./assets/fa-png/download.png";

        // don't dismount the input type file
        return (
            <div className="bulk-import-form" style={open ? {} : {display: "none"}}>
                {open && (
                    <div className="bulk-import-close">
                        <button className="css-reset" onClick={this.props.parent.toggleBulkImport}>Close</button>
                    </div>
                )}
                <table>
                    <tbody>
                    {open && (
                        <tr>
                            <td className="cell-msg">
                                <p>Download 1 or more template files based on the service type of the genes you
                                    want to import. To import more than 1 service type, upload multiple files one at
                                    a time.</p>
                            </td>
                            <td className="cell-templates">
                                <p><a title="Download Excel Template" href={this.props.gene_synthesis_url}><img
                                    src={downloadSrc} download/>{` `}Gene
                                    Synthesis Excel Template</a></p>
                                <p><a title="Download Excel Template" href={this.props.subcloning_url}><img
                                    src={downloadSrc} download/>{` `}Subcloning
                                    Only Excel Template</a></p>
                                <p><a title="Download Excel Template"
                                      href={this.props.mutagenesis_url}><img src={downloadSrc} download/>{` `}Mutagenesis
                                    Excel Template</a></p>
                            </td>
                        </tr>
                    )}
                    <tr>
                        <td className="cell-file">
                            <input ref={this.fileInput} type="file" name="bulk_import_file"/>
                        </td>
                        {open && (
                            <td className="cell-submit">
                                <div className="gq-btn btn-orange">
                                    <button onClick={this.submitFile}>Import</button>
                                </div>
                            </td>
                        )}
                    </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}
