import Generic from './Generic';

/**
 *
 */
export default class OrderFormItem extends React.Component {

    constructor(props) {
        super(props);

        // give access of this instantiated component to the OrderForm component
        this.props.callbacks.registerViaKey(this.props._key, this);

        this.setField = this.setField.bind(this);
        this.getField = this.getField.bind(this);
        this.renderOptions = this.renderOptions.bind(this);
        this.getVectorOptionGroupName = this.getVectorOptionGroupName.bind(this);
        this.scrollIntoView = this.scrollIntoView.bind(this);
        this.filterDnaSequence = this.filterDnaSequence.bind(this);
        this.filterProteinSequence = this.filterProteinSequence.bind(this);
        this.InvalidCountFromString = this.InvalidCountFromString.bind(this);
        this.onChangeBasic = this.onChangeBasic.bind(this);
        this.onChangeCheckbox = this.onChangeCheckbox.bind(this);
        this.onClickDeleteSelf = this.onClickDeleteSelf.bind(this);
        this.onClickToggleSelf = this.onClickToggleSelf.bind(this);
        this.onClickClearSelf = this.onClickClearSelf.bind(this);
        this.onClickEditSelf = this.onClickEditSelf.bind(this);
        this.Label = this.Label.bind(this);
        this.Checkbox = this.Checkbox.bind(this);
        this.Input = this.Input.bind(this);
        this.Textarea = this.Textarea.bind(this);
        this.TextareaPlus = this.TextareaPlus.bind(this);
        this.getButton = this.getButton.bind(this);
        this.renderErrors = this.renderErrors.bind(this);
        this.initMemoComponents = this.initMemoComponents.bind(this);
        this.getOptionName = window.geneQuotes.getOptionName.bind(window.geneQuotes);
        this.getOptionGroup = window.geneQuotes.getOptionGroup.bind(window.geneQuotes);
        this.getOptionFirst = window.geneQuotes.getOptionFirst.bind(window.geneQuotes);
        this.optionValueExists = window.geneQuotes.optionValueExists.bind(window.geneQuotes);

        this.memos = {};
        this.state = this.getInitialState();

        console.log("On construct initial state...", this.state);

        this.initMemoComponents();

        this.test = {
            render: 0,
            didUpdate: 0
        };

        // setField sets this to true. adds optimization shouldComponentUpdate.
        this.fieldWasSet = false;

        this.domNode = React.createRef();
        this.vectorFile = React.createRef();

        this.regex = {};
        this.regex.whiteSpace = /[\s]{1,}/;
        this.regex.stopAndAfter = /[s][t][o][p][\s\S]{0,}/gi;
        this.regex.notACGT = /[^ACGT]/gi;
        this.regex.notValidProteinSequence = /[^ARNDCEQGHILKMFPSTWYV*-]/gi;

        // this has some annoying line break issues unfortunately.
        // if changing this, you may need to check server side code which repeats this value.
        this.invalidReplacementCharacter = '';

        // gives "this" addTest and renderTests methods.
        Generic.provideTests(this);

        this.addTest("Set Many Fields", () => {
            this.setField('testField1', 'testValue1');
            this.setField('testField1', 'testValue1a');
            this.setField('testField1', 'testValue1b');
            this.setField('testField2', 'testValue2');
            this.setField('testField3', 'testValue3');
            this.setField('testField4', 'testValue4');
        });

        this.addTest("Dump", () => {
            console.log("Dump", this);
        });

        this.addTest("Update", () => {
            this.forceUpdate();
        });

        this.addTest("Random State", () => {
            this.setState({
                random: Math.random()
            });
        });

        this.addTest("Change Test Input State", () => {
            this.setState({testInput: Math.random()});
        });

        this.addTest("Redundant State", () => {
            this.setState({});
        });
    }

    // init memo components using arrow functions so that they still have
    // access to "this" without being provided it in props.
    // this also means that if we un-memo some components, we shouldn't
    // have to change much or anything at all in the render body.
    //
    // Breaking each order form item into something like 10 memo components
    // was not desirable, but was a necessary performance optimization.

    // In most cases, we ignore props passed into the components, and reference "this"
    // directly instead, which is in a way, breaking the rules, but unfortunately, I see
    // no other reasonable way. It would be enormously repetitive to pass in every single
    // prop directly in props and achieve literally nothing. Instead, we manually declare
    // the dependencies in props.dependency_{int}, when those props change, the component
    // will re-render.
    initMemoComponents() {

        this.memos.ItemHeader = React.memo(props => {
            return (
                <div className="item-header">
                    <div className="item-header-left">
                        {this.Checkbox({
                            name: 'checked',
                            value: '1',
                            checked: this.props.checked,
                            onChange: (e) => {
                                this.props.callbacks.setChecked(this.props.index, e.target.checked);
                            }
                        })}
                        {!this.props.isOrderSummary && (
                            <div className="name">
                                <this.Label name="gene_name">{this.props.index + 1}.</this.Label>
                                <this.Input name="gene_name" title="Gene Name" placeholder="Gene Name"/>
                            </div>
                        )}
                        {this.props.isOrderSummary && (
                            <p className="name-static">
                                <span className="number">{this.props.index + 1}. </span>
                                <span className="value">{this.getField('gene_name') || "____________"}</span>
                            </p>
                        )}
                        {this.props.isOrderSummary && (
                            <p className="price">
                                <span className="price-label">Price: </span>
                                <span className="price-value">{this.state.price ? "$" + this.state.price : "TBD"}</span>
                            </p>
                        )}
                        {!this.props.isOrderSummary && this.state.errors.length > 0 && (
                            <p className="errors-notice">{this.state.errors.length} Errors Found</p>
                        )}
                    </div>
                    <div className="item-header-right">
                        {!this.props.isOrderSummary && (
                            <button title="Toggle"
                                    className={"css-reset control control-collapse" + (this.getField('open') && ' rotated')}
                                    onClick={this.onClickToggleSelf}>
                                <img src="./assets/fa-png/chevron-down.png" alt=""/>
                            </button>
                        )}
                        {!this.props.isOrderSummary && (
                            <button title="Reset" className="css-reset control control-clear"
                                    onClick={this.onClickClearSelf}>
                                <img src="./assets/fa-png/eraser.png" alt=""/>
                            </button>
                        )}
                        {this.props.isOrderSummary && (
                            <button title="Edit" className="css-reset control control-edit"
                                    onClick={this.onClickEditSelf}>
                                <img src="./assets/fa-png/edit.png" alt=""/>
                            </button>
                        )}
                        <button title="Delete" className="css-reset control control-delete"
                                onClick={this.onClickDeleteSelf}>
                            <img src="./assets/fa-png/trash-o.png" alt=""/>
                        </button>
                    </div>
                </div>
            );
        });

        this.memos.SequenceAndServiceTypeWrapper = React.memo(props => {
            return (
                <>
                <div className="wrapper sequence-type">
                    <p className="label">Sequence Type</p>
                    <div className="options">
                        {this.getButton('sequence_type', 'dna', this.getOptionName('sequence_type', 'dna'))}
                        {this.getButton('sequence_type', 'protein', this.getOptionName('sequence_type', 'protein'), this.getField('service_type') !== 'gene_synthesis')}
                    </div>
                </div>
                <div className="wrapper service-type">
                    <p className="label">Service Type</p>
                    <div className="options">
                        {this.getButton('service_type', 'gene_synthesis', this.getOptionName('service_type', 'gene_synthesis'))}
                        {this.getButton('service_type', 'subcloning', this.getOptionName('service_type', 'subcloning'))}
                        {this.getButton('service_type', 'mutagenesis', this.getOptionName('service_type', 'mutagenesis'))}
                    </div>
                </div>
                </>
            );
        });

        this.memos.GeneSynthesisDnaWrapper = React.memo(props => {

            return (
                <div className="wrapper sequence service-type-gene-synthesis sequence-type-dna">
                    <this.TextareaPlus
                        doInvalid={true}
                        doLength={true}
                        lengthLabel="Length (DNA bases):"
                        name="dna_sequence"
                        label={(<strong>Sequence</strong>)}
                    />
                </div>
            );
        });

        this.memos.GeneSynthesisProteinWrapper = React.memo(props => {

            return (
                <div className="wrapper sequence svt-gene-synthesis sqt-protein">
                    <div className="sub-wrapper optional-5">
                        <label htmlFor={this.getHtmlId('protein_5_flanking_sequence')}>optional - 5"
                            Flanking
                            sequence: </label>
                        <this.Input name="protein_5_flanking_sequence"/>
                        {this.state.open && this.InvalidCountFromString(this.getField('protein_5_flanking_sequence'))}
                    </div>

                    <this.TextareaPlus
                        doInvalid={true}
                        doLength={false}
                        name="protein_sequence"
                        title="Enter protein sequence here"
                        placeholder="Enter protein sequence here"
                    />
                    <div className="sub-wrapper optional-3">
                        <label htmlFor={this.getHtmlId('protein_3_flanking_sequence')}>optional - 3"
                            Flanking
                            sequence: </label>
                        <this.Input name="protein_3_flanking_sequence"/>
                        {this.InvalidCountFromString(this.getField('protein_3_flanking_sequence'))}
                        <div className="length">
                            Length (DNA bases):{` `}
                            <strong>{this.state.open && this.getProteinTotalLength()}</strong>
                        </div>
                    </div>
                </div>
            );
        });

        this.memos.SubcloningSequenceTargetGene = React.memo(props => {
            return (
                <this.TextareaPlus
                    doInvalid={true}
                    doLength={true}
                    name="subcloning_sequence_target_gene"
                    label={(
                        <><strong className='td-underline'>Target Gene Sequence</strong> (gene of
                        interest
                        only
                        -
                        without plasmid)</>
                    )}
                />
            );
        });

        this.memos.SubcloningSequenceSourcePlasmid = React.memo(props => {
            return (
                <this.TextareaPlus
                    doInvalid={true}
                    doLength={true}
                    name="subcloning_sequence_source_plasmid"
                    label={(
                        <><strong className='td-underline'>Source</strong> <strong>Plasmid</strong> (or
                        DNA
                        fragment) <strong>+ Target Gene Sequence</strong></>
                    )}
                />
            );
        });

        this.memos.SubcloningSequenceFinalPlasmid = React.memo(props => {
            return (
                <this.TextareaPlus
                    doInvalid={true}
                    doLength={true}
                    name="subcloning_sequence_final_plasmid"
                    label={(
                        <><strong className="td-underline">Final</strong> <strong>Plasmid + Target Gene
                            Sequence</strong> (sequence of target gene in final plasmid)</>
                    )}
                />
            );
        });


        this.memos.MutagenesisSequenceFinal = React.memo(props => {

            return (
                <this.TextareaPlus
                    doInvalid={true}
                    doLength={true}
                    name="mutagenesis_sequence_final"
                    label={(
                        <>
                        <strong className="td-underline">Final</strong> <strong>Mutant
                            Sequence</strong> (including
                        plasmid if applicable)
                        </>
                    )}
                />
            );
        });

        this.memos.MutagenesisSequenceTemplate = React.memo(props => {

            return (
                <this.TextareaPlus
                    doInvalid={true}
                    doLength={true}
                    name="mutagenesis_sequence_template"
                    label={(
                        <>
                        <strong className="td-underline">Template</strong> <strong>Sequence
                            (WT)</strong>
                        </>
                    )}
                />
            );
        });

        // may be more expensive than it looks due to getting <select> options
        this.memos.MutationsSubWrapper = React.memo(props => {

            let active = this.getField('service_type') === 'mutagenesis';

            return (
                <div className="sub-wrapper mutations"
                     title={active ? '' : 'For Mutagenesis Only'}>
                    <label htmlFor={this.getHtmlId('mutations')}>Number of Mutations (or
                        deletions)</label>
                    <select name="mutations" value={this.getField('mutations') || ""} onChange={this.onChangeBasic}
                            disabled={active ? "" : "disabled"}>
                        {this.renderOptions('mutations')}
                    </select>
                </div>
            );
        });

        this.memos.DeliveryTypeWrapper = React.memo(props => {

            return (
                <div className="wrapper delivery-type">
                    <label>Delivery Type</label>
                    {this.Checkbox({
                        name: "delivery_lyophilized",
                        wrapperProps: {
                            title: "This delivery type is required."
                        },
                        id: this.getHtmlId('delivery_lyophilized'),
                        checked: true,
                        disabled: true,
                        label: this.getOptionName('delivery_type', 'delivery_lyophilized'),
                        onChange: this.onChangeCheckbox,
                    })}
                    {this.Checkbox({
                        name: "delivery_glycerol",
                        id: this.getHtmlId('delivery_glycerol'),
                        checked: this.getField('delivery_glycerol') === "1",
                        label: this.getOptionName('delivery_type', 'delivery_glycerol'),
                        onChange: (e => {
                            if (e.target.checked) {
                                this.setField('delivery_buffer', false);
                                this.setField('delivery_glycerol', "1");
                            } else {
                                this.setField('delivery_glycerol', false);
                            }
                        }),
                    })}
                    {this.Checkbox({
                        name: "delivery_buffer",
                        id: this.getHtmlId('delivery_buffer'),
                        checked: this.getField('delivery_buffer') === "1",
                        label: this.getOptionName('delivery_type', 'delivery_buffer'),
                        value: "1",
                        onChange: (e => {
                            if (e.target.checked) {
                                this.setField('delivery_glycerol', false);
                                this.setField('delivery_buffer', "1");
                            } else {
                                this.setField('delivery_buffer', false);
                            }
                        }),
                    })}
                </div>
            );
        });

        this.memos.DeliverySubWrapper = React.memo(props => {
            return (
                <div className="sub-wrapper delivery-amount">
                    <label htmlFor={this.getHtmlId('delivery_amount')}>Delivery Amount</label>
                    <select name="delivery_amount" value={this.getField('delivery_amount') || ""}
                            onChange={this.onChangeBasic}>
                        {this.renderOptions('delivery_amount')}
                    </select>
                </div>
            );
        });

        this.memos.KozakSequenceWrapper = React.memo(props => {

            let allow_optimize_and_kozak = props.allow_optimize_and_kozak;

            return (
                <div
                    className={"wrapper inline add-kozak-sequence" + ( allow_optimize_and_kozak ? "" : " disabled")}
                    title={allow_optimize_and_kozak ? "" : "Does not apply when service type is Mutagenesis"}>
                    <div className="sub-wrapper">
                        <p className="label">Add Kozak Sequence?</p>
                        <div className="options">
                            {this.Checkbox({
                                label: 'Yes',
                                name: 'add_kozak_sequence',
                                checked: allow_optimize_and_kozak && this.getField('add_kozak_sequence') === 'yes',
                                disabled: allow_optimize_and_kozak === false,
                                value: 'yes',
                            })}
                            {this.Checkbox({
                                label: 'No',
                                name: 'add_kozak_sequence',
                                checked: allow_optimize_and_kozak === false || this.getField('add_kozak_sequence') === 'no',
                                disabled: allow_optimize_and_kozak === false,
                                value: 'no',
                            })}
                        </div>
                    </div>
                    <div className="sub-wrapper">
                        <label htmlFor={this.getHtmlId('kozak_sequence')}>If Yes, Sequence?</label>
                        <this.Input
                            name="kozak_sequence"
                            value={allow_optimize_and_kozak ? this.getField("kozak_sequence") : ""}
                            placeholder="Enter Kozak Sequence"
                            disabled={allow_optimize_and_kozak === false || this.getField('add_kozak_sequence') !== 'yes'}
                        />
                    </div>
                </div>
            );
        });

        this.memos.OptimizeSequenceWrapper = React.memo(props => {

            let allow_optimize_and_kozak = props.allow_optimize_and_kozak;

            return (
                <div
                    className={"wrapper inline optimize-sequence" + ( allow_optimize_and_kozak ? "" : " disabled")}
                    title={allow_optimize_and_kozak ? "" : "Does not apply when service type is Mutagenesis"}>
                    <div className="sub-wrapper">
                        <p className="label">Optimize Sequence?</p>
                        <div className="options">
                            {this.Checkbox({
                                label: 'Yes',
                                name: 'optimize_sequence',
                                checked: allow_optimize_and_kozak && this.getField('optimize_sequence') === 'yes',
                                disabled: allow_optimize_and_kozak === false,
                                value: 'yes',
                            })}
                            {this.Checkbox({
                                label: 'No',
                                name: 'optimize_sequence',
                                checked: allow_optimize_and_kozak === false || this.getField('optimize_sequence') === 'no',
                                disabled: allow_optimize_and_kozak === false,
                                value: 'no',
                            })}
                        </div>
                    </div>
                    <div className="sub-wrapper for-expression-system">
                        <label htmlFor={this.getHtmlId('expression_system')}>If Yes, Expression System?</label>
                        <this.Input
                            name="expression_system"
                            value={allow_optimize_and_kozak ? this.getField("expression_system") : ""}
                            placeholder="Enter Intended Expression System"
                            disabled={allow_optimize_and_kozak === false || this.getField('optimize_sequence') !== 'yes'}
                        />
                    </div>
                    <div className="sub-wrapper for-restriction-sites">
                        <label htmlFor={this.getHtmlId('restriction_sites')}>If Yes, Restriction Sites to avoid
                            /
                            maintain?</label>
                        <this.Input
                            name="restriction_sites"
                            value={allow_optimize_and_kozak ? this.getField("restriction_sites") : ""}
                            placeholder="Enter restriction sites to avoid / maintain"
                            disabled={allow_optimize_and_kozak === false || this.getField('optimize_sequence') !== 'yes'}
                        />
                    </div>
                </div>
            );
        });

        // may be more expensive than it looks due to getting 90+ <select> options
        this.memos.VectorWrapper = React.memo(props => {

            console.log("VectorWrapper Render...", props);

            let vector = this.getField('vector');
            let vector_other = vector === 'other';

            // note: keep the file upload in the DOM when vector is not 'other'
            return (
                <div className="wrapper vector">
                    <div className="sub-wrapper vector-select">
                        <label htmlFor={this.getHtmlId('vector')}>Vector</label>
                        <select name="vector" value={vector || ""} onChange={this.onChangeBasic}>
                            {this.renderOptions(this.getVectorOptionGroupName(), "Choose a Vector")}
                        </select>
                    </div>
                    <div className="sub-wrapper vector-custom" style={vector_other ? {} : {display: "none"}}>
                        {vector_other && (
                            <>
                            <p className="msg"><em>Select vector above or enter vector details and attach
                                vector
                                sequence if not listed.</em></p>
                            <div className="text-items">
                                <div className="item">
                                    <this.Input name="vector_name" placeholder="Vector Name"
                                                title="Vector Name"/>
                                </div>
                                <div className="item">
                                    <this.Input name="vector_antibiotic_resistance"
                                                placeholder="Antibiotic Resistance"
                                                title="Antibiotic Resistance"/>
                                </div>
                                <div className="item">
                                    <this.Input name="vector_growth_conditions"
                                                placeholder="Growth Conditions"
                                                title="Growth Conditions"/>
                                </div>
                            </div>
                            </>
                        )}
                        <div className="file-item">
                            <input
                                type="file"
                                name="vector_file"
                                placeholder="Attach Vector"
                                title="Attach Vector"
                                ref={this.vectorFile}
                            />
                        </div>
                    </div>
                </div>
            );
        });

        this.memos.CloningSites = React.memo(props => {

            console.log("cloning sites render...", props);

            return (
                <div className="wrapper cloning-sites">
                    <p className="label">Cloning sites / method</p>
                    <table className="sub-wrapper cloning-sites-table">
                        <tbody>
                        <tr>
                            <td><label htmlFor={this.getHtmlId('cloning_sites_5')}>5' Restriction
                                Site</label>
                            </td>
                            <td>
                                <this.Input name="cloning_sites_5" placeholder="default is blunt-end"/>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <label htmlFor={this.getHtmlId('cloning_sites_3')}>3' Restriction
                                    Site</label>
                            </td>
                            <td>
                                <this.Input name="cloning_sites_3" placeholder="default is blunt-end"/>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            );
        });

        // this is probably far to small to have any benefit from using memo,
        // but it's here, so i'm not going to move it. Pass in a hardcoded
        // value into props, so that it never changes, and only renders once.
        // I believe that when passing in no props, re-render will occur always.
        // alternatively, we could specify the 2nd parameter of React.memo, which
        // would be a function which always returns true.
        this.memos.addItemButtonWrapper = React.memo(props => {

            // todo: insertion should go after the clicked item not at the end of the list.
            return (
                <div className="wrapper add-another">
                    <div className="gq-btn color-orange">
                        <button onClick={this.props.callbacks.insert}>Add Another</button>
                    </div>
                </div>
            )
        });
    }

    // very necessary performance optimization. Part of this comes from the fact that
    // some values used in the render function of the component are passed down from the parent.
    // therefore when we do things like, set one component to have a status (prop) of checked,
    // the entire parent has to re-render, which includes re-rendering all components. This is
    // very expensive in general, but we achieve very significant performance boosts by instead
    // just comparing props and state and then possibly doing nothing. Therefore, with 50 form
    // items, when we check one box, we'll re-render one item, and run this function on the other
    // 49 items. Still a pretty absurd amount of work just to check a box, but again, much better than
    // before. In the specific case of checking the boxes, we could use uncontrolled inputs which
    // would solve this one issue, but there are many other times when the entire order form
    // has to re-render.
    shouldComponentUpdate(nextProps, nextState) {

        // this is an optimization. If a field was set either with setField or with setState,
        // then eventually, we will figure that out and return true. But, if we used setField,
        // then we can return true immediately, without having to compare all props and state.
        if (this.fieldWasSet) {
            return true;
        }

        // nope, react actually doesn't expose this code to us and forces us to write our own
        // let ret = React.PureComponent.prototype.shouldComponentUpdate.call(this, nextProps, nextState );

        // most state changes will affect fieldWasSet, therefore, it may
        // be a tiny bt more efficient to compare props first, even though props do contain a lot of callback functions.
        if (!Generic.shallowCompare(nextProps, this.props)) {
            return true;
        }

        // now compare state...
        if (!Generic.shallowCompare(nextState, this.state)) {
            return true;
        }

        // lastly, errors are nested in state, so its important that we account for this here,
        // otherwise, adjusting errors will not result in updating the component
        if (!Generic.shallowCompare(nextState.errors, this.state.errors)) {
            return true;
        }

        // trying to see if/when we did all the work above when changing state in OrderForm
        // a.k.a why are react keys not working as I thought they would?
        console.log("Component should not update...");
        return false;
    }

    // kind of cheating and using this as "get derived state from state", so that in some cases
    // we can blindly set state to something invalid, and this should sort of validate it.
    // the downside is that this method is static so we wont have access to some methods.
    static getDerivedStateFromProps(props, state) {

        // object for updates to state
        let updates = {};

        let sq = state.sequence_type;
        let sv = state.service_type;

        // protein is only for gene synthesis
        if (sq === 'protein' && (sv === 'subcloning' || sv === 'mutagenesis')) {
            updates.sequence_type = 'dna';
        }

        // mutagenesis cannot select these custom options
        // in addition we may choose to hide the value of any input into the text boxes in render()
        if (sv === 'mutagenesis') {
            updates.optimize_sequence = 'no';
            updates.add_kozak_sequence = 'no';
        }

        // checking one box will uncheck the other but this only occurs with user interaction
        if (state.delivery_buffer && state.delivery_glycerol) {
            updates.delivery_glycerol = false;
            updates.delivery_buffer = false;
        }

        // strictly null to not update the state
        return updates || null;
    }

    // references global config and returns <options>'s
    renderOptions(groupName, placeholder) {
        return window.geneQuotes.renderOptions(this.getOptionGroup(groupName), placeholder);
    }

    // gets the option group name used to populate vector dropdown list with options, which
    // change based on service type. the values below must correspond to values used
    // in the server-side config.
    getVectorOptionGroupName(service_type = null) {

        if (service_type === null) {
            service_type = this.getField('service_type');
        }

        switch (service_type) {
            case 'gene_synthesis':
                return 'vector_gene_synthesis';
            case 'subcloning':
                return 'vector_subcloning';
            case 'mutagenesis':
                return 'vector_mutagenesis';
            default:
                return null;
        }
    }

    // jQueryTextAreaHighlight() {
    //     let test = $('#' + this.getHtmlId('protein_sequence'));
    //     if (test.length > 0) {
    //         test.highlightWithinTextarea({
    //             highlight: [
    //                 'stop',
    //             ]
    //         });
    //     }
    // }

    componentDidMount() {

        // this.jQueryTextAreaHighlight.apply(this);

        // for bulk import possibly
        if (this.props.onMount) {
            this.props.onMount(this);
        }

        // might be better to use this for bulk import ?
        if (this.props.initialState) {
            this.setState(this.props.initialState);
        }
    }

    // need function not a variable so we can return to initial state on clear.
    // a variable may get mutated..
    getInitialState() {

        let initialServiceType = 'gene_synthesis';

        return {
            open: true,
            sequence_type: 'protein',
            service_type: initialServiceType,
            optimize_sequence: 'no',
            add_kozak_sequence: 'no',
            delivery_lyophilized: true,
            errors: [],
            price: null,
            vector: this.getOptionFirst(this.getVectorOptionGroupName(initialServiceType)),
            mutations: this.getOptionFirst('mutations'),
            delivery_amount: this.getOptionFirst('delivery_amount'),
            protein_sequence: "",
        }
    }

    resetState() {

        this.setState((prevState) => {

            let newState = {};

            // can't simply omit the keys we want to removed. setState will never remove anything,
            // instead, we have to provide them with a value of undefined.
            Object.keys(prevState).map((key) => {
                newState[key] = undefined;
            });


            newState = Object.assign({}, newState, this.getInitialState());

            console.log("Reset...", newState);

            // object containing all keys in current and initial state
            return newState;
        });
    }

    componentDidUpdate(prevProps, prevState) {

        // re-validate vector options if service type changed
        if (prevState.service_type !== this.state.service_type) {

            // console.log("Check vectors...", prevState.service_type, this.state.service_type);

            // most likely different service types will have the same vector options
            if (!this.optionValueExists(this.getVectorOptionGroupName(), this.state.vector)) {
                // console.log("Vector option not valid", this.state.vector, this.getOptionGroup(this.getVectorOptionGroupName()));
                this.setState({
                    vector: this.getOptionFirst(this.getVectorOptionGroupName())
                });
            }
        }
    }

    getField(name, fromState) {
        // allow fromState to be {} although I don't anticipate ever needing this.
        return fromState !== undefined ? fromState[name] : this.state[name];
    }

    filterProteinSequence(sequence) {

        if (!sequence) {
            return '';
        }

        // order is pretty important
        sequence = sequence.replace(this.regex.whiteSpace, "", sequence);
        sequence = sequence.replace(this.regex.stopAndAfter, "", sequence);
        sequence = sequence.replace(this.regex.notValidProteinSequence, this.invalidReplacementCharacter, sequence);
        return sequence;
    }

    filterDnaSequence(sequence) {

        if (!sequence) {
            return '';
        }

        // order is pretty important
        sequence = sequence.replace(this.regex.whiteSpace, "", sequence);

        // I think that only protein sequences are likely to contain the word stop.
        // todo: confirm what to do about 'stop' here
        // sequence = sequence.replace( this.regex.stopAndAfter, "", sequence );

        sequence = sequence.replace(this.regex.notACGT, this.invalidReplacementCharacter, sequence);
        return sequence;
    }

    getStrlen(str, filterInvalid = false) {

        if (!str) {
            return 0;
        }

        if (filterInvalid && str.length > 0) {
            str = str.replace(new RegExp("[" + this.invalidReplacementCharacter + "]", 'gi'), "", str);
        }
        // console.log('getStrlen', value, str, filterInvalid, str.length );
        return str.length;
    }

    // checks 3 fields to compute total length with special rules
    getProteinTotalLength() {
        let len = 0;
        // todo: depending on rules for "stop" we may need to re-ensure this calculation is correct.
        // ...for example, if we somehow accomplish the miracle of adding spans around invalid characters
        // rather than using a single invalid character replacement.
        len += this.getStrlen(this.getField('protein_5_flanking_sequence'), true);
        len += this.getStrlen(this.getField('protein_3_flanking_sequence'), true);
        len += 3 * this.getStrlen(this.getField('protein_sequence'), true);
        return len;
    }

    // callback not optional
    filterField(name, value, then) {

        then = then || ((v) => {
            return v;
        });

        switch (name) {
            case 'protein_sequence':
                return then(this.filterProteinSequence(value));
                break;
            case 'dna_sequence':
            case 'protein_5_flanking_sequence':
            case 'protein_3_flanking_sequence':
            case 'subcloning_sequence_target_gene':
            case 'subcloning_sequence_source_plasmid':
            case 'subcloning_sequence_final_plasmid':
            case 'mutagenesis_sequence_template':
            case 'mutagenesis_sequence_final':
                return then(this.filterDnaSequence(value));
                break;
            default:
                return then(value);
        }
    };

    setField(name, value) {

        this.fieldWasSet = true;

        this.filterField(name, value, filteredValue => {
            this.setState({
                [name]: filteredValue
            });
        });
    }

    onChangeCheckbox(e) {
        this.setField(e.target.name, e.target.checked ? e.target.value : false);
    };

    onChangeBasic(e) {
        this.setField(e.target.name, e.target.value);
    };

    // undefined default value will not clear inputs after resetting the form...
    getNameAndValue(name, defaultValue) {
        defaultValue = defaultValue === undefined ? "" : defaultValue;
        return {
            name: name,
            value: this.getField(name) || defaultValue
        }
    };

    getHtmlId(name) {
        return name + "" + this.props._key;
    };

    onClickDeleteSelf(e) {

        // prevent possible form submission
        if (e) {
            e.preventDefault();
        }

        if (confirm("Are you sure you want to delete this entry?")) {
            this.props.callbacks.deleteViaKey(this.props._key);
        }
    };

    onClickEditSelf(e) {

        e && e.preventDefault();

        // tell the parent to handle editing this item
        this.props.callbacks.editViaKey(this.props._key);
    };

    onClickClearSelf(e) {

        // prevent possible form submission
        if (e) {
            e.preventDefault();
        }

        if (confirm("Are you sure you want to reset this entry?")) {
            this.resetState();
        }
    };

    onClickToggleSelf(e) {

        // prevent possible form submission
        if (e) {
            e.preventDefault();
        }

        this.setState((prev) => {
            return {
                open: !prev.open
            }
        });
    };

    getButton(name, value, text, disabled) {

        let cls = 'gq-btn';

        const onClick = (e) => {
            if (!disabled) {
                this.setField(name, value);
            }
        };

        const checked = this.getField(name) === value;
        cls += checked ? ' active' : '';
        cls += disabled ? ' disabled' : '';

        return (
            <div key={value} className={cls}>
                <button onClick={onClick}>{text}</button>
            </div>
        );
    };

    // pure component reads only props
    static InvalidCountFromProps(props) {
        return (
            <p className="invalid-count" style={props.count > 0 ? {} : {display: "none"}}>
                {props.count} invalid characters were found and replaced with {props.char}
            </p>
        );
    }

    // shows the length of string and possibly the number of "invalid replacement characters" and
    InvalidCountFromString(value) {

        let lengthRaw = this.getStrlen(value, false);
        let lengthFiltered = this.getStrlen(value, true);
        let invalidCount = parseInt(lengthRaw - lengthFiltered);
        let invalidChar = this.invalidReplacementCharacter;

        // console.log("InvalidCountFromString", lengthRaw, lengthFiltered, invalidCount );

        return this.constructor.InvalidCountFromProps({
            count: invalidCount,
            char: invalidChar,
        });
    };

    // prints label, textarea, possibly invalid count error, and possibly the length of the value,
    TextareaPlus({label, doLength, doInvalid, lengthLabel, ...props}) {

        let value = this.getField(props.name);

        // apparently, lengthLabel is not defined by default in function below ... ?
        let _lengthLabel = lengthLabel;

        let length = doLength && (() => {

            let lengthFiltered = this.getStrlen(value, true);
            let _lengthLabel = _lengthLabel || "Length:";

            return (
                <div className="length">{_lengthLabel}{` `}<strong>{lengthFiltered}</strong></div>
            );
        })();

        let cls = "sub-wrapper " + "for-" + props.name;

        return (
            <div className={cls}>
                {label && (<this.Label name={props.name}>{label}</this.Label>)}
                <this.Textarea id={this.getHtmlId(props.name)} {...props} />
                {doInvalid && this.InvalidCountFromString(value)}
                {length || ""}
            </div>
        );
    }

    // outputs <label htmlFor={this.getHtmlId( props.name )} />
    // but you can just write <this.Label name="" />
    // is it worth it? probably not.
    Label({name, children, ...props}) {

        let p = Object.assign({}, props, {
            htmlFor: typeof (props.htmlFor) !== 'undefined' ? props.htmlFor : this.getHtmlId(name)
        });

        return (
            <label {...p}>{children}</label>
        )
    }

    // prints div containing label and input type checkbox
    Checkbox({disabled, wrapperProps, label, ...props}) {

        wrapperProps = wrapperProps || {};
        let value = props.value || "1";

        let p = Object.assign({}, props, {
            id: props.id || (this.getHtmlId(props.name) + "_" + value),
            checked: typeof (props.checked) !== 'undefined' ? props.checked : props.value && this.getField(props.name) === props.value,
            value: value,
            onChange: props.onChange || this.onChangeCheckbox,
            type: props.type || 'checkbox'
        });

        let cls = 'gq-checkbox';
        cls += p.inline ? ' inline' : '';
        cls += p.checked ? ' checked' : '';
        cls += disabled ? ' disabled' : '';

        return (
            <div className={cls} {...wrapperProps}>
                <input {...p} />
                {label && (<label htmlFor={p.id}>{label}</label>)}
            </div>
        )
    }

    // just a textarea
    Textarea(props) {

        let p = Object.assign({}, props, {
            id: props.id || this.getHtmlId(props.name),
            onChange: props.onChange || this.onChangeBasic,
            value: typeof (props.value) === 'undefined' ? this.getField(props.name) : props.value
        });

        // if we don't do this, we cannot easily clear the field
        p.value = p.value || "";

        return (
            <textarea {...p} />
        );
    };

    // just an input
    Input({_ref, ...props}) {

        let p = Object.assign({}, props, {
            id: props.id || this.getHtmlId(props.name),
            type: props.type || 'text',
            onChange: props.onChange || this.onChangeBasic,
            value: typeof( props.value ) !== 'undefined' ? props.value : this.getField(props.name)
        });

        if (_ref) {
            p.ref = _ref;
        }

        return (
            <input {...p} />
        );
    }

    scrollIntoView() {
        this.domNode.scrollIntoView();
    }

    renderErrors() {

        // maybe return nothing instead of printing a hidden div
        if (this.state.errors.length < 1) {
            return;
        }

        return (
            <div className="item-errors" style={this.state.errors.length > 0 ? {} : {display: "none"}}>
                <div className="item-errors-inner">
                    {this.state.errors.map(error => {
                        return (<p>{error}</p>)
                    })}
                </div>
                <p className="item-errors-extra">
                    {this.props.isOrderSummary && (
                        <button className="css-reset fix-errors" onClick={this.onClickEditSelf}>Fix Errors</button>
                    )}
                    {!this.props.isOrderSummary && (
                        <button className="css-reset save-changes"
                                onClick={e => this.props.callbacks.sendToServer('calculate_price', {
                                    savedItemKey: this.props._key
                                })}>Save</button>
                    )}
                </p>
            </div>
        );
    }

    renderOrderSummaryBody() {

        let sqt = this.getField('sequence_type');
        let svt = this.getField('service_type');
        let optimize = this.getField('optimize_sequence') === 'yes';
        let kozak = this.getField('add_kozak_sequence') === 'yes';
        let vector = this.getField('vector');

        let showDeliveryType = (field) => {
            let v = this.getField(field);
            return v && (
                <p>{this.getOptionName('delivery_type', field)}</p>
            );
        };

        // counts valid and invalid chars in input
        let length = (input) => {

            let total = this.getStrlen(input, false);
            let valid = this.getStrlen(input, true);
            let invalid = total - valid;

            return (
                <>
                {total}
                {invalid > 0 && <span className="invalid-length"> + {invalid} invalid</span>}
                </>
            )
        };

        return (
            <div className="item-body is-order-summary">
                {this.renderErrors()}
                <div className="cols">
                    <div className="col col-sequence">
                        <div className="grp grp-sequence">
                            <p className="title">Sequence</p>
                            <p>{this.getOptionName('sequence_type', sqt)}</p>
                        </div>
                        <div className="grp grp-optimize">
                            <p>
                                <span className="v1">Optimize: {optimize ? "Yes, " : "No"}</span>
                                {optimize && <span className="v2">{this.getField('expression_system')}</span>}
                                {optimize && <span className="v3">{this.getField('restriction_sites')}</span>}
                            </p>
                        </div>
                        <div className="grp grp-kozak">
                            <p>
                                <span className="v1">Kozak: {kozak ? "Yes, " : "No"}</span>
                                {kozak && <span className="v2">{this.getField('kozak_sequence')}</span>}
                            </p>
                        </div>
                    </div>
                    <div className="col col-service">
                        <div className="grp grp-service">
                            <p className="title">Service</p>
                            <p>{this.getOptionName('service_type', svt)}</p>
                        </div>
                    </div>
                    <div className="col col-length">
                        <div className="grp grp-service">
                            <p className="title">Length (bp)</p>
                            {svt === 'gene_synthesis' && sqt === 'dna' &&
                            <p>{length(this.getField('dna_sequence'))}</p>}

                            {svt === 'gene_synthesis' && sqt === 'protein' &&
                            <p>{length(this.getField('protein_5_flanking_sequence'))}</p>}

                            {svt === 'gene_synthesis' && sqt === 'protein' &&
                            <p>{length(this.getField('protein_sequence'))} (x3)</p>}

                            {svt === 'gene_synthesis' && sqt === 'protein' &&
                            <p>{length(this.getField('protein_3_flanking_sequence'))}</p>}

                            {svt === 'subcloning' && <p>{length(this.getField('subcloning_sequence_target_gene'))}</p>}

                            {svt === 'subcloning' &&
                            <p>{length(this.getField('subcloning_sequence_source_plasmid'))}</p>}

                            {svt === 'subcloning' &&
                            <p>{length(this.getField('subcloning_sequence_final_plasmid'))}</p>}

                            {svt === 'mutagenesis' && <p>{length(this.getField('mutagenesis_sequence_template'))}</p>}

                            {svt === 'mutagenesis' && <p>{length(this.getField('mutagenesis_sequence_final'))}</p>}
                        </div>
                    </div>
                    <div className="col col-vector">
                        <div className="grp grp-vector">
                            <p className="title">Vector</p>
                            {vector !== 'other' && <p>{this.getField('vector')}</p>}

                            {vector === 'other' && <p>{this.getField('vector_name')}</p>}
                            {vector === 'other' && <p>{this.getField('vector_antibiotic_resistance')}</p>}
                            {vector === 'other' && <p>{this.getField('vector_growth_conditions')}</p>}
                            {/*{todo: Detect if file is attached and show that here}*/}
                        </div>
                    </div>
                    <div className="col col-amount">
                        <div className="grp grp-amount">
                            <p className="title">Amount</p>
                            <p>{this.getField('delivery_amount')}</p>
                        </div>
                    </div>
                    <div className="col col-cloning-mutations">
                        <div className="grp grp-cloning">
                            <p className="title">Cloning Sites</p>
                            <p>5': {this.getField('cloning_sites_5') || "Blunt end cloning"}</p>
                            <p>3': {this.getField('cloning_sites_3') || "Blunt end cloning"}</p>
                        </div>
                        <div className="grp grp-mutations">
                            <p className="title">Mutations / Deletions</p>
                            <p>{sqt === 'mutagenesis' ? this.getField('mutations') : "-"}</p>
                        </div>
                    </div>
                    <div className="col col-delivery-type">
                        <div className="grp grp-delivery-type">
                            {showDeliveryType('delivery_lyophilized')}
                            {showDeliveryType('delivery_glycerol')}
                            {showDeliveryType('delivery_buffer')}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    render() {

        this.fieldWasSet = false;
        this.test.render++;

        // we use these a lot so local var is easier
        let sequence_type = this.getField('sequence_type');
        let service_type = this.getField('service_type');

        let allow_optimize_and_kozak = service_type !== 'mutagenesis';

        let cls = 'order-form-item';
        cls += this.state.errors.length > 0 ? ' has-errors' : '';

        console.time('itemRender' + this.props._key);

        const ret = (
            <div className={cls} ref={this.domNode}>
                {/*{Generic.Debugger("OrderFormItem", this)}*/}
                {/*{this.renderTests()}*/}
                <this.memos.ItemHeader
                    dependency_1={this.props.checked}
                    dependency_2={this.props.index}
                    dependency_3={this.props.isOrderSummary}
                    dependency_4={this.getField('gene_name')}
                    dependency_5={this.state.open}
                    dependency_6={this.state.price}
                    dependency_7={this.state.errors.length}
                />

                {this.props.isOrderSummary ? this.renderOrderSummaryBody() : (

                    <div className="item-body" style={this.state.open ? {} : {display: 'none'}}>

                        {this.renderErrors()}

                        <this.memos.SequenceAndServiceTypeWrapper
                            dependency_1={sequence_type}
                            dependency_2={service_type}
                        />

                        {service_type === 'gene_synthesis' && sequence_type === 'dna' && (
                            <this.memos.GeneSynthesisDnaWrapper
                                dependency_1={this.getField('dna_sequence')}
                            />
                        )}
                        {service_type === 'gene_synthesis' && sequence_type === 'protein' && (

                            <this.memos.GeneSynthesisProteinWrapper
                                dependency_1={this.getField('protein_5_flanking_sequence')}
                                dependency_2={this.getField('protein_sequence')}
                                dependency_3={this.getField('protein_3_flanking_sequence')}
                            />
                        )}
                        {service_type === 'subcloning' && (
                            <div className="wrapper sequence svt-subcloning">

                                <this.memos.SubcloningSequenceTargetGene
                                    dependency_1={this.getField('subcloning_sequence_target_gene')}
                                />

                                <this.memos.SubcloningSequenceSourcePlasmid
                                    dependency_1={this.getField('subcloning_sequence_source_plasmid')}
                                />

                                <this.memos.SubcloningSequenceFinalPlasmid
                                    dependency_1={this.getField('subcloning_sequence_final_plasmid')}
                                />

                            </div>
                        )}

                        {service_type === 'mutagenesis' && (
                            <div className="wrapper sequence svt-mutagenesis">

                                <this.memos.MutagenesisSequenceTemplate
                                    dependency_1={this.getField('mutagenesis_sequence_template')}
                                />

                                <this.memos.MutagenesisSequenceFinal
                                    dependency_1={this.getField('mutagenesis_sequence_final')}
                                />

                            </div>
                        )}

                        <this.memos.OptimizeSequenceWrapper
                            allow_optimize_and_kozak={allow_optimize_and_kozak}
                            dependency_2={this.getField('optimize_sequence')}
                            dependency_3={this.getField('expression_system')}
                            dependency_4={this.getField('restriction_sites')}
                        />

                        <this.memos.KozakSequenceWrapper
                            allow_optimize_and_kozak={allow_optimize_and_kozak}
                            dependency_2={this.getField('add_kozak_sequence')}
                            dependency_3={this.getField('kozak_sequence')}
                        />

                        <div className="bottom-row">

                            <this.memos.VectorWrapper
                                dependency_1={this.getField('vector')}
                                dependency_2={this.getField('vector_name')}
                                dependency_3={this.getField('vector_antibiotic_resistance')}
                                dependency_4={this.getField('vector_growth_conditions')}
                            />

                            <this.memos.CloningSites
                                dependency_1={this.getField('cloning_sites_3')}
                                dependency_2={this.getField('cloning_sites_5')}
                            />

                            <div className="wrapper mutations-delivery">

                                <this.memos.MutationsSubWrapper
                                    dependency_1={this.getField('mutations')}
                                    dependency_2={service_type}
                                />

                                <this.memos.DeliverySubWrapper
                                    dependency_1={this.getField('delivery_amount')}
                                />

                            </div>

                            <this.memos.DeliveryTypeWrapper
                                dependency_1={this.getField('delivery_lyophilized')}
                                dependency_2={this.getField('delivery_glycerol')}
                                dependency_3={this.getField('delivery_buffer')}
                            />

                            <this.memos.addItemButtonWrapper
                                dependency_1="dummy_variable_ie_always_used_cached_value"
                            />

                        </div>
                    </div>
                )}
            </div>
        );

        console.timeEnd('itemRender' + this.props._key);

        return ret;
    }
}

